import React, { Component } from 'react'
import Input from '../UI/Input/Input';
import Button from '../UI/Button/Button';
import Spinner from '../UI/Spinner/Spinner';
import classes from './Auth.css';
import { Redirect } from "react-router-dom";
import { connect } from 'react-redux';
import * as actions from '../../store/actions/index';
class Auth extends Component {
   state = {
       controls: {
        email: {
            elementType: 'input',
            elementConfig: {
                type: 'email',
                placeholder: 'Mail Address'
            },
            value: '',
            validation: {
                required: true,
                isEmail: true
            },
            valid: false,
            touched: false
        },
        password: {
            elementType: 'input',
            elementConfig: {
                type: 'password',
                placeholder: 'Password'
            },
            value: '',
            validation: {
                required: true,
                minLength: 6
            },
            valid: false,
            touched: false
        }
       },
       isSignup : false
   }

   componentDidMount(){
    if(!this.props.buildingBurger && this.props.authRedirectPath !== '/'){
        this.props.onSetAuthRedirectPath();
    }
   }
   checkValidity(value, rules) {
    let isValid = true;
    if (!rules) {
        return true;
    }
    
    if (rules.required) {
        isValid = value.trim() !== '' && isValid;
    }

    if (rules.minLength) {
        isValid = value.length >= rules.minLength && isValid
    }

    if (rules.maxLength) {
        isValid = value.length <= rules.maxLength && isValid
    }

    if (rules.isEmail) {
        const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
        isValid = pattern.test(value) && isValid
    }

    if (rules.isNumeric) {
        const pattern = /^\d+$/;
        isValid = pattern.test(value) && isValid
    }

    return isValid;
    }
    inputChangedHandler = (event, controlName) => {
        const updatedControls = {
            ...this.state.controls,
            [controlName] : {
                ...this.state.controls[controlName],
                value : event.target.value,
                valid : this.checkValidity(event.target.value, this.state.controls[controlName].validation),
                touched : true
             }
        };
        this.setState({controls:updatedControls});
    };
    submitHandler = (event) => {
        event.preventDefault();
        this.props.onAuth(this.state.controls.email.value, this.state.controls.password.value, this.state.isSignup); 
    }
    switchAuthModeHandler = () => {
        this.setState(prevState => {
            return {isSignup : !prevState.isSignup};
        }); 
    }
    render() {
        const formElementsArray = [];
        for (let key in this.state.controls) {
            formElementsArray.push({
                id: key,
                config: this.state.controls[key]
            });
        }
            let form = formElementsArray.map(formElement => (
                <Input 
                key={formElement.id}
                elementType={formElement.config.elementType}
                elementConfig={formElement.config.elementConfig}
                value={formElement.config.value}
                invalid={!formElement.config.valid}
                shouldValidate={formElement.config.validation}
                touched={formElement.config.touched}
                changed={(event) => this.inputChangedHandler(event, formElement.id)} />
            ));
            if(this.props.loading){
                form = <Spinner />
            }
            let errorMessage = null;
            if(this.props.error){
                errorMessage = this.props.error.message;
            }
            let authRedirect = null;
            if(this.props.isAuthenticate){
                authRedirect = <Redirect to = {this.props.authRedirectPath} />
            }
        return (
            <div className={classes.Auth}>
            {authRedirect}
            <p className={classes.err}>{errorMessage}</p>
                <form onSubmit={this.submitHandler}>
                    {form}
                    <Button btnType="Success">{this.state.isSignup ? 'Sign Up' : 'Login'}</Button>
                </form>
                <Button 
                clicked = {this.switchAuthModeHandler}
                btnType="Danger">Switch To {this.state.isSignup ? 'Login' : 'Sign Up'}</Button>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return {
        // token : state.auth.token,
        // userId : state.auth.userId,
        error : state.auth.error,
        loading : state.auth.loading,
        isAuthenticate : state.auth.token != null,
        buildingBurger : state.burgerBuilder.building,
        authRedirectPath : state.auth.authRedirectPath
    }
}
const mapDispatchToProps = dispatch => {
     return {
        onAuth : (email, pass, isSignup) => dispatch(actions.auth(email, pass, isSignup)),
        onSetAuthRedirectPath : () => dispatch(actions.setAuthRedirectPath('/'))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Auth);


